
import { __ } from '@wordpress/i18n';
import { Modal } from '@wordpress/components';

import './ProModal.scss';

const ProModal = ({ isProModal, setIsProModal, children, pluginName, priceLink }) => {

	return isProModal && <Modal className='bBlocksProModal' title={<Title title={pluginName} priceLink={priceLink} />} onRequestClose={() => setIsProModal(false)}>
		<h3>Enhance your experience with Pro features of the {pluginName} </h3>

		<ul className='features'>
			{children}
		</ul>
		<UpgradeBtn priceLink={priceLink} />
	</Modal>
}
export default ProModal;

const UpgradeBtn = ({ priceLink }) => <a className='upgradeNow' href={priceLink} target='_blank' rel='noreferrer'>{__('Upgrade Now', 'mail-collection')}</a>

const Title = ({ title, priceLink = { priceLink } }) => <>
	{__(`Upgrade to ${title} Pro for advanced features`, 'mail-collection')}
</>